import React from "react";
import "font-awesome/css/font-awesome.css";
import styles from "./Cards.module.css";
const Cards = ({ jokes, fetchJoke }) => {
  return (
    <div className={styles.card}>
      <ul className={styles.list}>
        <li className={styles.listItem}>
          <p className={styles.text}>
            <i className="fa fa-angle-double-right"> </i> {jokes.joke}{" "}
          </p>
          <p className={styles.category}>-{jokes.category}</p>
        </li>
      </ul>
      <button className={styles.button} onClick={fetchJoke}>
        Get More Joke <i className="fa fa-smile-o"></i>
      </button>
    </div>
  );
};
export default Cards;
