import React, { useState, useEffect } from 'react';

import './App.css';
import { fetchJokes } from './api/index';
import Cards from './components/Cards/Cards';

function App() {
  const [jokes, setJokes] = useState('')
  useEffect(() => {
    Jokes()

  }, [])
  const Jokes = async () => {
    const joke = await fetchJokes()
    setJokes(joke)
  }
  const urlTwitter = "https://twitter.com/IbnuSacad"

  return (
    <div className="App">
      <h1 className="title"><i className="fa fa-assistive-listening-systems"></i> Jokes</h1>
      <Cards jokes={jokes} fetchJoke={Jokes} />
      <p><a href={urlTwitter} target="__blank" title="My Twitter">@ibnusacad</a></p>

    </div>
  );
}

export default App;
